'use client';

import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import Image from 'next/image';
import { X, Clock, Users, MapPin, ArrowRight } from 'lucide-react';
import { siteData, siteDataFr } from '@/data/siteData';

export const DailyExpeditions = ({ locale, dict }: { locale: string, dict?: any }) => {
    const data = locale === 'fr' ? siteDataFr : siteData;
    const excursions = data.excursions;
    const [selectedExcursion, setSelectedExcursion] = useState<any>(null);

    return (
        <section className="py-24 bg-neutral-50 relative overflow-hidden">
            <div className="container mx-auto px-6 md:px-12 relative z-10">
                {/* Header */}
                <div className="flex flex-col md:flex-row md:items-end justify-between mb-16 gap-8">
                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        whileInView={{ opacity: 1, y: 0 }}
                        viewport={{ once: true }}
                    >
                        <span className="text-primary font-bold uppercase tracking-[0.3em] text-[10px] mb-4 block">
                            Short Adventures
                        </span>
                        <h2 className="text-4xl md:text-5xl lg:text-6xl font-medium font-playfair text-neutral-dark leading-none">
                            Daily <span className="italic text-primary">Expeditions.</span>
                        </h2>
                    </motion.div>

                    <motion.p
                        initial={{ opacity: 0, x: 20 }}
                        whileInView={{ opacity: 1, x: 0 }}
                        viewport={{ once: true }}
                        className="text-neutral-medium max-w-sm text-sm leading-relaxed border-l border-neutral-light pl-6"
                    >
                        Curated one-day journeys departing from Marrakech. Experience the diversity of Morocco from sunrise to sunset.
                    </motion.p>
                </div>

                {/* Grid of Expeditions */}
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                    {excursions.map((excursion, index) => (
                        <motion.div
                            key={excursion.id}
                            initial={{ opacity: 0, y: 30 }}
                            whileInView={{ opacity: 1, y: 0 }}
                            viewport={{ once: true }}
                            transition={{ delay: index * 0.1 }}
                            layoutId={`card-${excursion.id}`}
                            onClick={() => setSelectedExcursion(excursion)}
                            className="group cursor-pointer bg-white rounded-none border border-neutral-light hover:border-primary/50 transition-all duration-500 overflow-hidden shadow-sm hover:shadow-xl"
                        >
                            <div className="relative aspect-[4/3] overflow-hidden">
                                <motion.div layoutId={`image-${excursion.id}`} className="w-full h-full relative">
                                    <Image
                                        src={excursion.image}
                                        alt={excursion.name}
                                        fill
                                        className="object-cover transition-transform duration-700 group-hover:scale-110"
                                    />
                                </motion.div>
                                <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-transparent to-transparent opacity-60 group-hover:opacity-40 transition-opacity duration-500" />

                                <div className="absolute bottom-6 left-6 text-white">
                                    <div className="flex items-center gap-2 text-[10px] font-bold uppercase tracking-widest mb-2 opacity-80">
                                        <Clock className="w-3 h-3" />
                                        <span>1 Day</span>
                                    </div>
                                    <motion.h3
                                        layoutId={`title-${excursion.id}`}
                                        className="text-2xl font-playfair font-medium leading-tight"
                                    >
                                        {excursion.name}
                                    </motion.h3>
                                </div>

                                <div className="absolute top-6 right-6 bg-white/10 backdrop-blur-md border border-white/20 p-2 rounded-full opacity-0 group-hover:opacity-100 transition-opacity duration-300 transform translate-y-2 group-hover:translate-y-0">
                                    <ArrowRight className="w-4 h-4 text-white" />
                                </div>
                            </div>

                            <div className="p-6">
                                <p className="text-neutral-medium text-sm line-clamp-2 leading-relaxed mb-6">
                                    {excursion.description}
                                </p>
                                <div className="flex items-center justify-between border-t border-neutral-light pt-4">
                                    <div className="flex items-center gap-2">
                                        <span className="text-[10px] font-bold uppercase tracking-widest text-neutral-400">From</span>
                                        <span className="text-xl font-playfair font-bold text-primary">€{excursion.pricing?.[0]?.totalPrice}</span>
                                    </div>
                                    <span className="text-xs font-bold uppercase tracking-widest text-neutral-dark group-hover:text-primary transition-colors">
                                        View Details
                                    </span>
                                </div>
                            </div>
                        </motion.div>
                    ))}
                </div>

                {/* Modal Detail View */}
                <AnimatePresence>
                    {selectedExcursion && (
                        <div className="fixed inset-0 z-[100] flex items-center justify-center p-4 md:p-8">
                            <motion.div
                                initial={{ opacity: 0 }}
                                animate={{ opacity: 1 }}
                                exit={{ opacity: 0 }}
                                onClick={() => setSelectedExcursion(null)}
                                className="absolute inset-0 bg-black/80 backdrop-blur-sm"
                            />

                            <motion.div
                                layoutId={`card-${selectedExcursion.id}`}
                                className="bg-white w-full max-w-5xl max-h-[90vh] overflow-y-auto relative rounded-sm shadow-2xl flex flex-col md:flex-row"
                                onClick={(e) => e.stopPropagation()}
                            >
                                <button
                                    onClick={() => setSelectedExcursion(null)}
                                    className="absolute top-4 right-4 z-50 bg-white/20 backdrop-blur-md p-2 rounded-full hover:bg-white hover:text-black text-white transition-all md:text-neutral-dark md:bg-white md:shadow-md"
                                >
                                    <X className="w-6 h-6" />
                                </button>

                                {/* Image Side */}
                                <div className="w-full md:w-1/2 relative h-[300px] md:h-auto min-h-[400px]">
                                    <motion.div layoutId={`image-${selectedExcursion.id}`} className="w-full h-full relative">
                                        <Image
                                            src={selectedExcursion.image}
                                            alt={selectedExcursion.name}
                                            fill
                                            className="object-cover"
                                        />
                                        <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent md:hidden" />
                                    </motion.div>
                                    <div className="absolute bottom-8 left-8 text-white md:hidden">
                                        <motion.h3
                                            layoutId={`title-${selectedExcursion.id}`}
                                            className="text-3xl font-playfair font-medium"
                                        >
                                            {selectedExcursion.name}
                                        </motion.h3>
                                    </div>
                                </div>

                                {/* Content Side */}
                                <div className="w-full md:w-1/2 p-8 md:p-12 flex flex-col bg-white">
                                    <motion.div
                                        initial={{ opacity: 0, y: 20 }}
                                        animate={{ opacity: 1, y: 0 }}
                                        transition={{ delay: 0.2 }}
                                    >
                                        <span className="hidden md:block text-primary font-bold uppercase tracking-[0.3em] text-[10px] mb-4">
                                            Daily Expedition
                                        </span>
                                        <h3 className="hidden md:block text-3xl lg:text-4xl font-playfair font-medium text-neutral-dark mb-6 leading-tight">
                                            {selectedExcursion.name}
                                        </h3>

                                        <div className="flex flex-wrap gap-6 mb-8 border-b border-neutral-light pb-8">
                                            <div className="flex items-center gap-3">
                                                <Clock className="w-5 h-5 text-primary" />
                                                <div className="flex flex-col">
                                                    <span className="text-[10px] text-neutral-400 font-bold uppercase tracking-wider">Duration</span>
                                                    <span className="text-sm font-medium text-neutral-dark">1 Day</span>
                                                </div>
                                            </div>
                                            <div className="flex items-center gap-3">
                                                <MapPin className="w-5 h-5 text-primary" />
                                                <div className="flex flex-col">
                                                    <span className="text-[10px] text-neutral-400 font-bold uppercase tracking-wider">Start/End</span>
                                                    <span className="text-sm font-medium text-neutral-dark">{data.location}</span>
                                                </div>
                                            </div>
                                            <div className="flex items-center gap-3">
                                                <span className="text-2xl font-playfair font-bold text-primary">€{selectedExcursion.pricing?.[0]?.totalPrice}</span>
                                            </div>
                                        </div>

                                        <div className="prose prose-neutral mb-8">
                                            <p className="text-neutral-medium leading-relaxed">
                                                {selectedExcursion.description}
                                            </p>
                                        </div>

                                        {selectedExcursion.itinerary && (
                                            <div className="mb-8 bg-neutral-50 p-6 rounded-sm border border-neutral-light">
                                                <h4 className="font-playfair font-bold text-lg mb-4 text-neutral-dark">Itinerary Highlights</h4>
                                                <ul className="space-y-4">
                                                    {selectedExcursion.itinerary.map((item: any, idx: number) => (
                                                        <li key={idx} className="flex gap-4">
                                                            <span className="w-1.5 h-1.5 bg-primary rounded-full mt-2 shrink-0" />
                                                            <p className="text-sm text-neutral-medium leading-relaxed">{item.description}</p>
                                                        </li>
                                                    ))}
                                                </ul>
                                            </div>
                                        )}

                                        <a
                                            href={`https://wa.me/212661123456?text=I'm interested in the ${selectedExcursion.name} excursion`}
                                            target="_blank"
                                            rel="noopener noreferrer"
                                            className="w-full bg-neutral-dark text-white py-4 px-8 font-bold uppercase tracking-[0.2em] text-xs hover:bg-primary transition-colors text-center block"
                                        >
                                            Book This Expedition
                                        </a>

                                    </motion.div>
                                </div>
                            </motion.div>
                        </div>
                    )}
                </AnimatePresence>
            </div>
        </section>
    );
};
